<template>
  <div>
    <panel title="เพิ่มข้อมูลพนักงาน">
      <div class="ml-5 my-3">
        <p>กรอกข้อมูลส่วนตัว</p>
      </div>
      <div class="vx-row mr-5 ml-5">
        <form-input
          label="ชื่อ-สกุล"
          v-model="form.name"
          v-validate="`required`"
          :col="4"
        />
        <form-input
          label="รหัสพนักงาน"
          v-model="form.code"
          v-validate="`required`"
          :col="4"
        />
        <custom-tree-select
          class="vx-col my-5"
          url="/api/setting/master/user"
          v-model="form.setting_master_users_id"
          :col="4"
        >
        </custom-tree-select>
      </div>

      <div class="vx-row mr-5 ml-5">
        <form-input
          label="สาขา"
          v-model="form.branch"
          v-validate="`required`"
          :col="4"
        />
      </div>

      <div class="my-3 ml-5">
        <p>กรอกข้อมูลบัญชีผู้ใช้</p>
      </div>

      <div class="vx-row mr-5 ml-5">
        <form-input
          label="อีเมล"
          v-model="form.email"
          v-validate="`required`"
          :col="4"
        />
        <form-input
          label="ชื่อบัญชีผู้ใช้"
          v-model="form.username"
          v-validate="`required`"
          :col="4"
        />
        <form-input
          label="รหัสผ่าน"
          v-model="form.password"
          v-validate="`required`"
          :col="4"
        />
      </div>

      <div class="ml-5 my-5">
        <p>เพิ่มข้อมูลติดต่อ</p>
      </div>
      <div>
        <div align="right" class="mb-3">
          <vs-button @click="activePrompt1 = true" color="primary" type="filled"
            >เพิ่มข้อมูล</vs-button
          >
        </div>
        <vs-prompt
          title="เพิ่มข้อมูลติดต่อ"
          color="success"
          @accept="acceptInsertContact"
          @close="close"
          :active.sync="activePrompt1"
        >
          <div class="con-exemple-prompt">
            <div>ประเภท</div>
            <form-input label="ประเภท" v-model="tempContact.type" :col="12" />
            <form-input label="ค่า" v-model="tempContact.value" :col="12" />
          </div>
        </vs-prompt>

        <vs-table pagination max-items="5" :data="form.user_contacts">
          <template slot="thead">
            <vs-th>ลำดับ</vs-th>
            <vs-th>ชื่อตำแหน่ง</vs-th>
            <vs-th></vs-th>
          </template>
          <template slot-scope="{ data }">
            <vs-tr :key="indextr" v-for="(tr, indextr) in data">
              <vs-td>
                {{ data[indextr].type }}
              </vs-td>
              <vs-td>
                {{ data[indextr].value }}
              </vs-td>
              <vs-td>
                <div class="flex mb-3" align="center">
                  <div class="w-1/6 bg-grid-color h-12">
                    <vs-button
                      color="danger"
                      type="flat"
                      icon-pack="feather"
                      icon="icon-trash"
                      @click.stop="openConfirmDelete(indextr)"
                    ></vs-button>
                  </div>
                  <div class="w-1/6 bg-grid-color-secondary h-12"></div>
                </div>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </div>

      <div class="ml-5 my-5">
        <p>แนบรูปถ่ายหน้าตรง</p>
      </div>
      <div class="vx-row ml-5">
        <attached :id="data_id" :model="data_model" ref="upload"></attached>
      </div>
      <vs-row class="my-5">
        <vs-col vs-type="flex" vs-justify="flex-end" vs-align="center">
          <vs-button class="mr-3" @click="InsertUserData()">บันทึก</vs-button>
          <router-link to="/employee/Index">
            <vs-button color="danger" type="filled">ยกเลิก</vs-button>
          </router-link>
        </vs-col>
      </vs-row>
      <pre>{{form}}</pre>
    </panel>
  </div>
</template>
<script>
export default {
  data () {
    return {
      activePrompt1: false,
      data_id: '',
      data_model: 'App\\Models\\User',
      users: [],
      form: {
        name: '',
        email: '',
        username: '',
        password: '',
        branch: '',
        code: '',
        setting_master_users_id: '',
        user_contacts: []
      },

      tempDeleteIndex: '',
      tempContact: {
        type: '',
        value: ''
      }
    }
  },
  methods: {
    async InsertUserData () {
      try {
        const result = await this.$validator.validateAll()
        if (result) {
          await this.$vs.dialog({
            type: 'confirm',
            color: 'success',
            title: 'คุณต้องการเพิ่มข้อมูลหรือไม่',
            text: `เพิ่มข้อมูลพนักงานชื่อ ${this.form.name}`,
            acceptText: 'ยืนยัน',
            cancelText: 'ยกเลิก',
            accept: this.acceptInsert
          })
        } else {
          await this.$vs.dialog({
            color: 'danger',
            title: 'สถานะการเพิ่มข้อมูล',
            text: 'เพิ่มข้อมูลพนักงานไม่สำเร็จ',
            acceptText: 'ปิด'
          })
        }
      } catch (error) {
        console.log('err', error)
      }
    },
    async acceptInsert () {
      try {
        const resp = await this.$http.post('/api/user', this.form)
        this.data_id = resp.data.data.id
        this.$refs.upload.id = resp.data.data.id

        const first = await this.$refs.upload.submit()
        console.log(first)

        await this.$vs.notify({
          color: 'success',
          title: 'ยืนยันการเพิ่มข้อมูล',
          text: 'เพิ่มข้อมูลสำเร็จ'
        })

        await this.$router.push({ name: 'index_em', query: {} })
      } catch (error) {
        throw error
      }
    },
    acceptInsertContact () {
      this.form.user_contacts.push({ ...this.tempContact })
      this.$vs.notify({
        color: 'success',
        title: 'ยืนยันการเพิ่มข้อมูล',
        text: 'เพิ่มข้อมูลสำเร็จ'
      })
    },
    openConfirmDelete (indextr) {
      this.tempDeleteIndex = indextr
      this.deleteUserContact = this.form.user_contacts[indextr].value
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: 'คุณต้องการลบข้อมูลใช่หรือไม่',
        text: `ลบข้อมูลติดต่อ ${this.deleteUserContact}`,
        accept: this.acceptDelete,
        acceptText: 'ยืนยัน',
        cancelText: 'ยกเลิก'
      })
    },
    async acceptDelete () {
      await this.form.user_contacts.splice(this.tempDeleteIndex, 1)
      await this.$vs.notify({
        color: 'danger',
        title: 'ยืนยันการลบข้อมูล',
        text: 'ลบข้อมูลสำเร็จ'
      })
    },
    close () {}
  }
}
</script>

